import React, { useState } from "react"
import { BsFillCameraFill } from "react-icons/bs"
import { Carousel } from "react-bootstrap"
import { estateUpdateAsync } from "../utilities/utilities"
import Header from "../components/header"
import { HomePageItems } from "../utilities/data"
import { imageUpdateAsync } from "../utilities/utilities"
import ConfirmationModal from "../components/confirmation-modal"
import ResultModal from "../components/result-modal"

const RealestateEdit = (props) => {
    const estate = props.location.estateData
    const [pending, setPending] = useState(false)
    const [error, setError] = useState(false)
    const [success, setSuccess] = useState(false)
    const [showConfirmationModal, setShowConfirmationModal] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)
    const [source, setSource] = useState({
        captureImage1: estate.image1
            ? `https://pwa.tidewater.ir:2026/estate/image/${estate.image1}`
            : "",
        captureImage2: estate.image2
            ? `https://pwa.tidewater.ir:2026/estate/image/${estate.image2}`
            : "",
        captureImage3: estate.image3
            ? `https://pwa.tidewater.ir:2026/estate/image/${estate.image3}`
            : "",
    })
    const [imageFile, setImageFile] = useState({
        captureImage1: null,
        captureImage2: null,
        captureImage3: null,
    })
    const [name, setName] = useState(estate.name)
    const [address, setAddress] = useState(estate.address)
    const [group, setGroup] = useState(estate.group)
    const [areaAyan, setAreaAyan] = useState(estate.areaAyan)
    const [yearofConstruction, setYearofConstruction] = useState(estate.yearofConstruction)
    const [identityNumber, setIdentityNumber] = useState(estate.identityNumber)
    const [location, setLocation] = useState(estate.location)
    const [documentStatus, setDocumentStatus] = useState(estate.documentStatus)
    const [documentType, setDocumentType] = useState(estate.documentType)
    const [ownership, setOwnership] = useState(estate.ownership)
    const [beneficiary, setBeneficiary] = useState(estate.beneficiary)
    const [usage, setUsage] = useState(estate.usage)
    const [areaArse, setAreaArse] = useState(estate.areaArse)
    const [price, setPrice] = useState(estate.price)
    const [lift, setLift] = useState(estate.lift ? true : false)
    const [parking, setParking] = useState(estate.parking ? true : false)
    const [storage, setStorage] = useState(estate.storage ? true : false)
    const id = estate.id
    const company = estate.company.name
    const title = estate.title
    const facilities = estate.facilities

    const homeItemId = HomePageItems.find((item) => {
        return item.name === company
    }).id

    const handleCapture = (target) => {
        if (target.files) {
            if (target.files.length !== 0) {
                const file = target.files[0]
                const newUrl = URL.createObjectURL(file)
                setImageFile({ ...imageFile, [target.name]: file })
                setSource({ ...source, [target.name]: newUrl })
            }
        }
    }

    const submitHandler = async (event) => {
        event.preventDefault()
        setPending(true)
        try {
            await estateUpdateAsync(
                id.toString(),
                name,
                company,
                title,
                location,
                beneficiary,
                group,
                documentType,
                identityNumber.toString(),
                price.toString(),
                areaArse.toString(),
                areaAyan.toString(),
                ownership,
                facilities,
                usage,
                address,
                lift,
                parking,
                storage,
                yearofConstruction.toString()
            )
            await imageUpdateAsync(
                id.toString(),
                imageFile.captureImage1,
                imageFile.captureImage2,
                imageFile.captureImage3
            )
            setError(false)
            setSuccess(true)
            setPending(false)
            setShowConfirmationModal(false)
            setShowResultModal(true)
        } catch (error) {
            setSuccess(false)
            setError(true)
            setPending(false)
            console.log(error)
        }
    }

    return (
        <div>
            <Header backLink={`/realestate-list/${company}`} homeItemId={homeItemId} />
            <div className="home-wrapper overflow-auto pb-4 pt-68">
                <Carousel
                    interval={null}
                    prevLabel=""
                    nextLabel=""
                    nextIcon={null}
                    prevIcon={null}
                    className="mb-0"
                    defaultActiveIndex={0}
                >
                    <Carousel.Item>
                        <label
                            htmlFor="capture-image-1"
                            className={`add-photo-container d-block ${
                                source.captureImage1 ? "h-350" : "pt-150"
                            } mx-auto`}
                        >
                            {source.captureImage1 && (
                                <div className="view-photo-container">
                                    <img
                                        src={source.captureImage1}
                                        style={{ width: "100%", height: "auto" }}
                                        alt={"snap"}
                                    />
                                </div>
                            )}
                            {!source.captureImage1 && (
                                <div className="text-center">
                                    <BsFillCameraFill className="h3" />
                                    <br />
                                      
                                </div>
                            )}
                            {source.captureImage1 && (
                                <div className="image-badge">    </div>
                            )}
                            <input
                                accept="image/*"
                                id="capture-image-1"
                                name="captureImage1"
                                type="file"
                                className="invisible h-0"
                                onChange={(e) => handleCapture(e.target)}
                                disabled={pending}
                            />
                        </label>
                    </Carousel.Item>
                    <Carousel.Item>
                        <label
                            htmlFor="capture-image-2"
                            className={`add-photo-container d-block ${
                                source.captureImage2 ? "h-350" : "pt-150"
                            } mx-auto`}
                        >
                            {source.captureImage2 && (
                                <div className="view-photo-container">
                                    <img
                                        src={source.captureImage2}
                                        style={{ width: "100%", height: "auto" }}
                                        alt={"snap"}
                                    />
                                </div>
                            )}
                            {!source.captureImage2 && (
                                <div className="text-center">
                                    <BsFillCameraFill className="h3" />
                                    <br />
                                      
                                </div>
                            )}
                            {source.captureImage2 && (
                                <div className="image-badge">    </div>
                            )}
                            <input
                                accept="image/*"
                                id="capture-image-2"
                                name="captureImage2"
                                type="file"
                                className="invisible h-0"
                                onChange={(e) => handleCapture(e.target)}
                                disabled={pending}
                            />
                        </label>
                    </Carousel.Item>
                    <Carousel.Item>
                        <label
                            htmlFor="capture-image-3"
                            className={`add-photo-container d-block ${
                                source.captureImage3 ? "h-350" : "pt-150"
                            } mx-auto`}
                        >
                            {source.captureImage3 && (
                                <div className="view-photo-container">
                                    <img
                                        src={source.captureImage3}
                                        style={{ width: "100%", height: "auto" }}
                                        alt={"snap"}
                                    />
                                </div>
                            )}
                            {!source.captureImage3 && (
                                <div className="text-center">
                                    <BsFillCameraFill className="h3" />
                                    <br />
                                      
                                </div>
                            )}
                            {source.captureImage3 && (
                                <div className="image-badge">    </div>
                            )}
                            <input
                                accept="image/*"
                                id="capture-image-3"
                                name="captureImage3"
                                type="file"
                                className="invisible h-0"
                                onChange={(e) => handleCapture(e.target)}
                                disabled={pending}
                            />
                        </label>
                    </Carousel.Item>
                </Carousel>
                <div className="col-10 mx-auto p-0 pt-2">
                    <div className="row m-0">
                        <div className="col-12 text-center text-light mb-3">
                            <span className="bg-danger">
                                {error ? "    " : ""}
                            </span>
                            <span className="bg-success">
                                {success ? "     " : ""}
                            </span>
                        </div>
                    </div>
                    <div className="row m-0">
                        <form className="col-12 p-0">
                            <input
                                type="text"
                                placeholder="     *"
                                className="form-control fs-large custom-input"
                                onChange={(e) => setName(e.target.value)}
                                value={name}
                                disabled={pending}
                            />
                            <input
                                type="text"
                                placeholder="    *"
                                className="form-control mt-2 custom-input"
                                onChange={(e) => setAddress(e.target.value)}
                                value={address}
                                disabled={pending}
                            />
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <label className="col-12 text-danger font-weight-bold my-3">
                                    *      
                                </label>
                            </div>
                            <div className="row flex-nowrap of-scroll sb-hidden text-center">
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">*</h6>
                                    <select
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) => setGroup(e.target.value)}
                                        value={group}
                                        disabled={pending}
                                    >
                                        <option disabled>      </option>
                                        <option value="apartmant"></option>
                                        <option value="mostaghelat"></option>
                                        <option value="zamin"></option>
                                        <option value="amozeshgah"></option>
                                        <option value="tejari"></option>
                                    </select>
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">* </h6>
                                    <input
                                        type="text"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) => setAreaAyan(e.target.value)}
                                        value={areaAyan}
                                        disabled={pending}
                                    />
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">* </h6>
                                    <input
                                        type="text"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) => setYearofConstruction(e.target.value)}
                                        value={yearofConstruction}
                                        disabled={pending}
                                    />
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">* </h6>
                                    <input
                                        type="text"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) => setIdentityNumber(e.target.value)}
                                        value={identityNumber}
                                        disabled={pending}
                                    />
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">* </h6>
                                    <input
                                        type="text"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) => setLocation(e.target.value)}
                                        value={location}
                                        disabled={pending}
                                    />
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">* </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) => setDocumentStatus(e.target.value)}
                                        value={documentStatus}
                                        disabled={pending}
                                    >
                                        <option disabled>      </option>
                                        <option value="azad"></option>
                                        <option value="darrahn"> </option>
                                        <option value="bazdasht"></option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 text-danger mb-2">
                                <div className="col-6 font-weight-bold my-auto">* </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control text-danger custom-input custom-input-border"
                                        onChange={(e) => setDocumentType(e.target.value)}
                                        value={documentType}
                                        disabled={pending}
                                    >
                                        <option disabled>      </option>
                                        <option value="takbarg"> </option>
                                        <option value="daftarche"> </option>
                                        <option value="gholname">  </option>
                                        <option value="sayer"></option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 text-danger mb-2">
                                <div className="col-6 font-weight-bold my-auto">*</div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control text-danger custom-input custom-input-border"
                                        onChange={(e) => setOwnership(e.target.value)}
                                        value={ownership}
                                        disabled={pending}
                                    >
                                        <option disabled>      </option>
                                        <option value=" ">
                                             
                                        </option>
                                        <option value=""></option>
                                        <option value=" "> </option>
                                        <option value=" "> </option>
                                        <option value=""></option>
                                        <option value=" "> </option>
                                        <option value="  ">  </option>
                                        <option value=" "> </option>
                                        <option value="  ">  </option>
                                        <option value="  ">  </option>
                                        <option value=" "> </option>
                                        <option value="  ">  </option>
                                        <option value=" ( )">( )</option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 text-danger mb-2">
                                <div className="col-6 font-weight-bold my-auto">*</div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control text-danger custom-input custom-input-border"
                                        onChange={(e) => setBeneficiary(e.target.value)}
                                        value={beneficiary}
                                        disabled={pending}
                                    >
                                        <option disabled>      </option>
                                        <option value=" ">
                                             
                                        </option>
                                        <option value=""></option>
                                        <option value=" "> </option>
                                        <option value=" "> </option>
                                        <option value=""></option>
                                        <option value=" "> </option>
                                        <option value="  ">  </option>
                                        <option value=" "> </option>
                                        <option value="  ">  </option>
                                        <option value="  ">  </option>
                                        <option value=" "> </option>
                                        <option value="  ">  </option>
                                        <option value=" ( )">( )</option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">*</div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) => setUsage(e.target.value)}
                                        value={usage}
                                        disabled={pending}
                                    >
                                        <option disabled>      </option>
                                        <option value="edari"></option>
                                        <option value="maskooni"></option>
                                        <option value="tejari"></option>
                                        <option value="zamin"></option>
                                        <option value="amoozeshi"></option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto"> </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <input
                                        type="text"
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) => setAreaArse(e.target.value)}
                                        value={areaArse}
                                        disabled={pending}
                                    />
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                      ( )
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <input
                                        type="text"
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) => setPrice(e.target.value)}
                                        value={price}
                                        disabled={pending}
                                    />
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-12 font-weight-bold my-auto">
                                    
                                    <br />
                                    <label>
                                        <input
                                            type="checkbox"
                                            onChange={(e) => setLift(e.target.checked)}
                                            checked={lift}
                                            disabled={pending}
                                        />
                                        <span className="text-secondary my-auto mr-2"></span>
                                    </label>
                                    <label className="mr-2">
                                        <input
                                            type="checkbox"
                                            onChange={(e) => setParking(e.target.checked)}
                                            checked={parking}
                                            disabled={pending}
                                        />
                                        <span className="text-secondary my-auto mr-2"></span>
                                    </label>
                                    <label className="mr-2">
                                        <input
                                            type="checkbox"
                                            onChange={(e) => setStorage(e.target.checked)}
                                            checked={storage}
                                            disabled={pending}
                                        />
                                        <span className="text-secondary my-auto mr-2"></span>
                                    </label>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="text-center">
                                <button
                                    type="button"
                                    className="btn btn-primary font-weight-bold col-6 mt-3"
                                    onClick={() => {
                                        setShowConfirmationModal(true)
                                    }}
                                >
                                    
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            {showConfirmationModal ? (
                <ConfirmationModal
                    handler={submitHandler}
                    show={showConfirmationModal}
                    action={"editEstate"}
                    pending={pending}
                    onHide={() => {
                        setShowConfirmationModal(false)
                    }}
                />
            ) : null}
            {showResultModal ? <ResultModal show={showResultModal} action={"editEstate"} /> : null}
        </div>
    )
}

export default RealestateEdit
